@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin-top: 5rem;
    .campo {
        display: flex; 
        align-items: center;
        margin-bottom: 2rem;

        label {
            text-align: right;
            margin-right: 1rem;
            flex: 0 0 8rem;
            @include m.telefono-chico{
                flex: 0 0 5rem;
            }
        }
        input {
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: lighten(v.$gris, 40%);

        }
    }

    input[type="submit"]{
        
        margin-top: 2rem;
    }
}

