@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal {
    position: absolute;
    background-color: rgb( 0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        border-radius: 1rem;

        .campo{
            margin: 0;
        }

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }
        input[type="text"] {
            border: 1px solid v.$gris;
            border-radius: .5rem;
        }
        .opciones {
            display: block;
            .submit-nueva-tarea,
            .cerrar-modal {
                flex: 0 0 5rem;
                margin-top: 1rem;
            }
            .cerrar-modal {
                @include m.boton(v.$naranja, v.$blanco);
            }
            .submit-nueva-tarea {
                @include m.boton(v.$indigo, v.$blanco);
            }
            @include m.tablet{
                display: flex;
                justify-content: space-between;
            }
        }


        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }
        .contador-tarea{
            padding-left: 10rem;
            flex: 1;
        }
    }
}