@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea {
    display: block;
    justify-content: space-evenly;
    align-items: center;

    @include m.tablet{
        display: flex;
    }

    .agregar-tarea {
        @include m.boton(v.$cyan, v.$blanco);
        font-size: 1.2rem;
        @include m.telefono{
            font-size: 1.6rem;
        }
    }
    .info{
        position: relative;
        font-size: 2.4rem;
        p{
            display: none;
        }
    }
    .info:hover{
        p{
            text-indent: 5rem;
            top: 50%;
            left: 5%;
            @include m.tablet{
                left: -500%;
                top: 60%;
            }
            width: 22rem;
            font-size: 0.8rem;
            position: absolute;
            background-color: rgba(20,20,20, .7);
            display: block;
            color: v.$blanco;
            border-radius: 1rem;
            padding: 0.5rem;
            span{
                font-weight: 700;
                text-decoration: underline;
            }

        }
    }
}

.listado-tareas {
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    .no-tareas {
        text-align: center;
    }
}

.tarea {
    
    padding: .5rem 2rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5%);
    margin-bottom: 1rem;
    transition: 1s ease;
    display: grid;
    grid-template-columns: 3fr 1fr;
    align-items: center;
   
    &.animAbajo{
        transform: translateY(125%);
    }

    &.animArriba{
        transform: translateY(-125%);
    }

    &:first-of-type {
        border-top: 2px solid darken(v.$grisClaro, 5%);;
    }

    .posicion{
        grid-column: 2 / 3;
        grid-row: 1 / 3;
        text-align: center;
        gap: 1rem;
        cursor: pointer;
        user-select:none;

        @include m.telefono{
            display: flex;
            justify-content: space-between;
        }
    }

    p {
        grid-column: 1 / 2;
        grid-row: 1;
        font-size: 1.2rem;
        overflow: hidden;
        word-wrap: break-word;
        hyphens: auto;
        @include m.tablet{
            flex: 0.9
        }
    }

    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }



    .opciones {
        grid-column: 1 / 2;
        grid-row: 2;

        button {
            border: none;
            font-size: .9rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 1rem;

            &:hover {
                cursor: pointer;
            }
        }
        .completa {
            background-color: v.$azul;
            color: v.$blanco;
        }
        .pendiente {
            background-color: v.$naranja;
            color: v.$blanco;
        }
        .eliminar-tarea {
            margin-top: 0.5rem;
            background-color: v.$rojo;
            color: v.$blanco;
            @include m.tablet{
                margin-left: 0.5rem;
                margin-top: 0;
            }
        }
        @include m.tablet{
            display: flex;
        }
    }
}