@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile {
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.desktop {
        display: none;
    }

    h1 {
        color: v.$blanco;
        margin: 0;
    }
    .menu {
        img {
            width: .8rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}

.barra {
    display: none;
    @include m.desktop {
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px rgba( 0 , 0 , 0, .4);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }



    .cerrar-sesion {
        @include m.boton( darken(v.$indigo, 20%), v.$blanco );
        font-size: 1.2rem;
        padding: 1rem;
        border-radius: .5rem;
    }
}

.info-usuario {
    display: block;
    @include m.desktop {
        display: flex;
        align-items: center;
    }
    .letra-nombre {
        font-weight: 700;
        color: v.$blanco;
        border-radius: 50%;
        width: 4.5rem;
        height: 4.5rem;
        font-size: 3rem;
        background-color: v.$indigo;
        margin-right: 1rem;
        text-align: center;
        user-select:none;
    }
    .texto-usuario{
        display: block;
        p{
            margin: 0;
            font-size: 1.4rem;
        }
    }
}