@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyectos {

    a {
        color: v.$indigo;
    }
}
.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: grid;
        grid-template-columns: 3fr 1fr;
        column-gap: 2rem;
        row-gap: 1rem;
        padding: 2rem;
        line-height: 2;
        border-radius: .5rem;

        &:hover {
            background-color: darken(v.$indigo, 5%);
        }

        a {
            grid-column: 1 / 2;
            grid-row: 1;
            color: v.$blanco;
            width: 100%;
            height: 100%;
        }

        .acciones-proyectos{
            grid-column: 2 / 3;
            grid-row: 1;
        }
        .texto-progreso{
            grid-column: 1 / 3;
            grid-row: 2;
            line-height: 1;
            color: v.$blanco;
            p{
                all: unset;
            }
        }
        .contenedor-progreso{
            grid-column: 1 / 3;
            grid-row: 3;
        }

    }
}

.renombrar-proyecto,
.eliminar-proyecto{
    text-align: center;
    margin: 1rem;
    font-size: 1.2rem;
    position: relative;
    width: 3rem;
    input{
        cursor: pointer;
        border: none;
        width: 100%;
    }
    .texto-alerta{
        display: none;
    }
}

.eliminar-proyecto{
    background-color: v.$naranja;
    input{
        background-color: v.$naranja;
    }
}

.renombrar-proyecto{
    background-color: v.$gris;
    input{
        background-color: v.$gris;
    }
}

.eliminar-proyecto:hover,
.renombrar-proyecto:hover{
    .texto-alerta{
        position: absolute;
        background-color: rgba(20,20,20, .7);
        display: block;
        color: v.$blanco;
        border-radius: 1rem;
        padding: 0.4rem;
    }
}

.eliminar-proyecto:hover{
    .texto-alerta{
        bottom: 90%;
    }
}

.renombrar-proyecto:hover{
    .texto-alerta{
        top: 90%;
    }
}

.contador-contenedor{
    padding-left: 10rem;
}